  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  div.product-name {
    font-size: x-large
  }

  div.product-info {
    margin-top: 4px;
    font-size: large;
    padding-left: 5px
  }

  div.product-image {
    padding: 10px
  }

  table, td, th {
    border: 1px solid black;
    border-collapse: collapse;
    padding: 5px
  }
  
  html {
    font-family: Arial, Helvetica, sans-serif;
  }

  select#set-selector {
    margin-bottom: 8px;
  }

  input#username, input#password {
    width: 130px
  }

form.form  { display: table;    }
p.form     { display: table-row;  }
label.form { display: table-cell; text-align: right; padding: 10px; }
input.form { display: table-cell; }